import React, { forwardRef } from "react";
import styled from "styled-components";

import { List } from "@radix-ui/react-tabs";

import { spacingMap } from "../styleguide";

import { TabsListProps } from "./Tabs.types";

const StyledList = styled(List)`
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	padding: 0px;
	gap: ${spacingMap.md};
`;

const TabsList = forwardRef<HTMLDivElement, TabsListProps>(({ children, dataQA, ...props }, ref) => (
	<StyledList ref={ref} data-qa={dataQA} {...props}>{children}</StyledList>
));

TabsList.displayName = "TabsList";

export default TabsList;
